.tms-em-chat-input {
    &.ui.segment {
        margin: 0;
        position: fixed;
        bottom: 0;
        left: 220px;
        right: 0;
        background-color: white;
        padding-bottom: 22px;
        @media only screen and (max-width: 767px) {
            left: 0;
        }
        .tms-upload-progress {
            position: absolute;
            bottom: 70px;
            right: 15px;
            left: 15px;
            .dz-preview {
                display: block!important;
                width: auto!important;
                background: #e0e1e2;
                margin: 0;
                padding: 7px;
            }
        }
        .ui[class*="left action"].input>textarea {
            border-top-left-radius: 0!important;
            border-bottom-left-radius: 0!important;
            border-left-color: transparent!important;
        }
        .textareaWrapper {
            max-width: 100%;
            -webkit-box-flex: 1;
            -webkit-flex: 1 0 auto;
            -ms-flex: 1 0 auto;
            flex: 1 0 auto;
            border: 1px solid rgba(34, 36, 38, .15);
            border-top-right-radius: .28571429rem;
            border-bottom-right-radius: .28571429rem;
            .CodeMirror,
            .CodeMirror-scroll {
                min-height: 0;
                margin-right: 35px;
                border: none;
            }
        }
        .ui.input textarea {
            resize: none;
            width: 100%;
            padding-right: 2.67142857em!important;
            margin: 0;
            max-width: 100%;
            outline: 0;
            -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
            text-align: left;
            display: block;
            // line-height: 1.2142em;
            padding: .67861429em 1em;
            background: #FFF;
            border: none;
            color: rgba(0, 0, 0, .87);
            box-shadow: none;
            border-top-right-radius: .28571429rem;
            border-bottom-right-radius: .28571429rem;
        }
    }
}

.tms-chat-direct {
    .tms-content {
        &.tms-sidebar-show {
            .tms-em-chat-input {
                right: 392px;
            }
        }
    }
}

.textcomplete-dropdown {
    position: static!important;
    border: 1px solid #ddd;
    background-color: white;
    list-style: none;
    padding: 0;
    margin: 0;
    border-radius: 5px;
    li {
        /* border-top: 1px solid #ddd; */
        padding: 2px 5px;
    }
    li:first-child {
        border-top: none;
        border-top-left-radius: 5px;
        border-top-right-radius: 5px;
    }
    li:last-child {
        border-bottom-left-radius: 5px;
        border-bottom-right-radius: 5px;
    }
    li:hover,
    .active {
        background-color: rgb(67, 159, 224);
    }
    a:hover {
        cursor: pointer;
    }
    li.textcomplete-item {
        a {
            color: black;
        }
        &:hover a,
        &.active a {
            color: white;
        }
    }
}
